import { useState, useEffect, FC } from 'react';
import { Tabs, Button, Input, Space } from '@sinohealth/butterfly-ui-antd/lib';
import { SyncOutlined } from '@ant-design/icons';
import styles from './index.less';
import { pharmacyStockStateCount } from '@/services/goods';
import TableData from './components/DataTable';
import SearchForm from './components/SearchForm';
import GlobalSettings from './components/GlobalSettings';
import AddMedicine from './components/AddMedicine';
import { useDict } from '@/hooks/useDict';

const { TabPane } = Tabs;

interface HeadSearch {
  baseSearchText?: string;
}

const GoodsList: FC<any> = (props: any) => {
  console.log(props);
  const [addMedicineVisiable, setAddMedicineVisiable] = useState<boolean>(false); // 新增药品
  const [globalSettingsVisiable, setglobalSettingsVisiable] = useState<boolean>(false); // 全局设置
  const [objHeadSearch, setObjHeadSearch] = useState<HeadSearch>({}); //  头部搜索内容
  const [tabStockStateCount, setTabStockStateCount] = useState<any>([]); // tab栏数据
  const [currType, setCurrType] = useState('1');
  const [thatTime, setThatTime] = useState(new Date());

  // 获取列表
  const getGoodsData = (value: string) => {
    setCurrType(value);
  };

  const fetchTabStockStateCount = async (params = {}) => {
    const result = await pharmacyStockStateCount(params);
    console.log('pharmacyStockStateCount', result);
    setTabStockStateCount(result);
  };

  const confirmSearch = (res: any) => {
    console.log(res);
    const options = { ...res };
    if (res.creationDate) {
      options.createTimeStart = res.creationDate[0];
      options.createTimeEnd = res.creationDate[1];
    }
    console.log('options==>', options);
    setObjHeadSearch(options);
    fetchTabStockStateCount(options);
  };

  useEffect(() => {
    fetchTabStockStateCount();
  }, []);

  const actionBar = (
    <Space>
      {/* <SyncOutlined /> */}
      <Button
        icon={<SyncOutlined />}
        onClick={() => {
          fetchTabStockStateCount();
          setThatTime(new Date());
        }}
      />
      <Button
        key="setting"
        onClick={() => {
          setglobalSettingsVisiable(true);
        }}
      >
        全局设置
      </Button>
      <Button
        key="add"
        type="primary"
        onClick={() => {
          setAddMedicineVisiable(true);
        }}
      >
        新增药品
      </Button>
    </Space>
  );

  return (
    <div className={styles.goods_list_page}>
      <SearchForm onFinish={(res) => confirmSearch(res)} />
      <div className={styles.data_content}>
        <Tabs defaultActiveKey="1" onChange={getGoodsData} tabBarExtraContent={actionBar} animated>
          {tabStockStateCount.map((v: any) => (
            <TabPane tab={`${v.name}(${v.num})`} key={v.type}>
              <TableData
                type={v.type}
                currType={currType}
                headSearch={objHeadSearch}
                onFetchTabStockStateCount={fetchTabStockStateCount}
                time={thatTime}
              />
            </TabPane>
          ))}
        </Tabs>
        <AddMedicine
          visibal={addMedicineVisiable}
          title="新增药品"
          cancel={(success) => {
            if (success) {
              setObjHeadSearch({}); //  清空高级搜索条件
              fetchTabStockStateCount();
              setThatTime(new Date());
            }
            setAddMedicineVisiable(false);
          }}
        />
        <GlobalSettings
          visibal={globalSettingsVisiable}
          title="全局设置"
          cancel={(update) => {
            if (update) {
              fetchTabStockStateCount();
              setThatTime(new Date());
            }
            setglobalSettingsVisiable(false);
          }}
        />
      </div>
    </div>
  );
};

export default GoodsList;
