import { useEffect, useState } from 'react';
import { Form, Input, message, Modal } from '@sinohealth/butterfly-ui-components/lib';
import { pharmacyChangeSalePrice } from '@/services/goods';
import styles from './index.less';

const waitTime = (time: number = 100) =>
  new Promise((resolve) => {
    setTimeout(() => {
      resolve(true);
    }, time);
  });
export type SalePriceModelDataType = {
  id: string | number;
  salePrice: string | number;
};
export type SalePriceModelType = {
  onCancel: (success?: boolean) => void;
  visible: boolean;
  data: SalePriceModelDataType;
};
export default (props: SalePriceModelType) => {
  const [loading, setLoading] = useState(false);
  const [form] = Form.useForm();
  useEffect(() => {
    if (props.data) {
      form.setFieldsValue({ currSalePrice: `￥${props.data.salePrice || '0'}`, salePrice: '' });
    }
  }, [props.data]);

  return (
    <Modal
      visible={props.visible}
      closable={false}
      cancelButtonProps={{ type: 'info' }}
      onOk={() => {
        form.submit();
      }}
      width={450}
      onCancel={() => {
        props.onCancel();
      }}
      confirmLoading={loading}
      className={styles['sale-price-model']}
    >
      <h2 className={styles.title}>修改零售价</h2>
      <Form
        form={form}
        layout="horizontal"
        labelCol={{ span: 5 }}
        onFinish={async (values: any) => {
          setLoading(true);
          await waitTime(2000);
          console.log(values);
          const params = {
            drugId: props.data.id,
            salePrice: values.salePrice || props.data.salePrice,
          };
          pharmacyChangeSalePrice(params)
            .then(() => {
              message.success('提交成功');
              setLoading(false);
              props.onCancel(true);
            })
            .catch(() => {
              setLoading(false);
            });
          return true;
        }}
      >
        <Form.Item name="currSalePrice" label="当前零售价" style={{ marginBottom: 10 }}>
          <Input bordered={false} />
        </Form.Item>
        <Form.Item
          name="salePrice"
          label="修改零售价"
          rules={[
            {
              pattern: /^[^ ]+$/g,
              message: '前后不能输入空格',
            },
            {
              pattern: /(^(\d|[1-9]\d{1,5})(\.\d{1,2})?$)|(^999999$)/,
              message: '请输入有效的零售价',
            },
          ]}
        >
          <Input placeholder="请输入" autoComplete="off" suffix="元" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
