import React, { useEffect, useRef } from 'react';
import {
  Row,
  Col,
  Form,
  Select,
  Button,
  Badge,
  message,
  Modal,
  Input,
  Checkbox,
  Descriptions,
  Divider,
} from '@sinohealth/butterfly-ui-antd/lib';
import styles from './index.less';
import MedicineSearch from '../MedicineSearch';
import { pharmacyDrugStockSave, queryByCommonNameOrApprovalNumber } from '@/services/goods';
import NotFoundContent from '../NotFoundContent';
import SearchIcon from '@/assets/images/goods/search.svg';

const { Option } = Select;

type AddMedicineProps = {
  title?: string;
  visibal?: boolean;
  cancel: (flag?: boolean, formVals?: any) => void;
};

interface MedicineValue {
  label: string;
  value: string;
  state: string;
}

// let medicineList: GOOD.drugStockInfo[] = [];
const medicineMap: any = {};

let submitType: 'on_sale' | 'off_sale' = 'on_sale';

const defaultMedcineInfo = {
  cfdaId: 0,
  state: '0',
};

const AddMedicine: React.FC<AddMedicineProps> = (props) => {
  const [form] = Form.useForm();
  const searchMedicineRef = useRef<{ clearOptions: any }>();
  const { title = '高级搜索', visibal, cancel } = props;
  const [searchText, setSearchText] = React.useState([]);
  const [searchType, setSearchType] = React.useState<'1' | '2'>('1');
  const [medicineInfo, setMedicineInfo] = React.useState<GOOD.drugStockInfo>(defaultMedcineInfo);

  async function fetchMedicineList(text: string): Promise<MedicineValue[]> {
    if (text === '') return [];
    const res: any = await queryByCommonNameOrApprovalNumber({
      searchText: text,
      type: searchType,
    });
    console.log(res);
    if (res) {
      // medicineList = res.result;
      const maps = res.map((v: any) => {
        const label =
          searchType === '1'
            ? `${v.commonName} | ${v.companyName} | ${v.productStandard}`
            : `${v.approvalNumber} | ${v.commonName} | ${v.companyName}`;

        medicineMap[v.specId] = v;
        return {
          label,
          value: v.specId,
          state: v.state,
        };
      });
      return maps;
    }
    return [];
  }

  useEffect(() => {
    console.log(medicineInfo);
  }, []);
  const cancelModel = () => {
    form.resetFields(); // 退出重置表单
    setSearchType('1');
    setMedicineInfo({ cfdaId: 0, state: '0' });
    setSearchText([]);
    cancel();
  };

  return (
    <Modal
      title={title}
      visible={visibal}
      onOk={() => {
        form.submit();
      }}
      width={700}
      maskClosable={false}
      footer={
        <>
          <Button
            type="primary"
            ghost
            onClick={() => {
              cancelModel();
            }}
          >
            取消
          </Button>
          {medicineInfo.state === '0' && (
            <>
              <Button
                type="primary"
                ghost
                disabled={!medicineInfo.drugName}
                onClick={() => {
                  submitType = 'off_sale';
                  form.submit();
                }}
              >
                保存（待上架）
              </Button>
              <Button
                type="primary"
                disabled={!medicineInfo.drugName}
                onClick={() => {
                  submitType = 'on_sale';
                  form.submit();
                }}
              >
                保存（直接上架）
              </Button>
            </>
          )}
        </>
      }
      onCancel={() => {
        cancelModel();
      }}
    >
      <Form
        form={form}
        layout="horizontal"
        labelCol={{ span: 6 }}
        onFinish={async (values: any) => {
          if (medicineInfo.state === '0') {
            const params: GOOD.pharmacyDrugStockSaveReq = {
              cfdaId: medicineInfo.cfdaId,
              stockQuantity: values.stockQuantity,
              salePrice: values.salePrice,
              spec: values.spec,
              productStandard: values.productStandard,
              stockState: 'limited',
              state: submitType,
              uniqueId: values.uniqueId,
              packageSpecification: values.packageSpecification,
            };
            if (values.stockState) {
              params.stockState = 'unlimited';
            }
            const res: any = await pharmacyDrugStockSave(params);
            if (res?.code === 200) {
              message.success('提交成功');
              searchMedicineRef?.current?.clearOptions();
              cancel(true);
            } else {
              cancel();
            }
          } else {
            cancel();
          }
          form.resetFields(); // 退出重置表单
          setSearchType('1'); // 重置添加类型
          setSearchText([]); // 搜索框重置
          setMedicineInfo(defaultMedcineInfo); // 清空添加弹窗数据
          return true;
        }}
      >
        <Row gutter={10} style={{ marginBottom: 16 }}>
          <Col span={3} style={{ marginTop: '5px' }}>
            快速搜索：
          </Col>
          <Col span={5}>
            <Select
              defaultValue="1"
              onChange={(value: any) => {
                setSearchType(value);
              }}
              style={{ width: '100%' }}
            >
              <Option value="1">药品通用名称</Option>
              <Option value="2">批准文号</Option>
            </Select>
          </Col>
          <Col span={16}>
            <Form.Item>
              <MedicineSearch
                value={searchText}
                ref={searchMedicineRef}
                placeholder="请输入药品通用名/批准文号"
                // eslint-disable-next-line react/jsx-no-bind
                fetchOptions={fetchMedicineList}
                onChange={(newValue) => {
                  console.log(medicineMap[newValue.value]);
                  form.setFieldsValue(medicineMap[newValue.value]);
                  setMedicineInfo({ ...medicineMap[newValue.value] });
                  setSearchText(newValue);
                  console.log(medicineInfo);
                }}
                style={{ width: '100%' }}
              />
            </Form.Item>
          </Col>
        </Row>
        {medicineInfo.drugName ? (
          <>
            <div className={styles.infoBox}>
              <div className={styles.title}>
                <span>药品基本信息</span>
                {medicineInfo.state === '0' ? (
                  <div className={styles.tagError}>未入库</div>
                ) : (
                  <div className={styles.tagSuccess}>已入库</div>
                )}
              </div>
              <Descriptions
                column={2}
                labelStyle={{ color: 'rgba(0, 0, 0, 0.45)', width: 75, justifyContent: 'end' }}
              >
                <Descriptions.Item label="药品名称">{medicineInfo.drugName}</Descriptions.Item>
                <Descriptions.Item label="商品名">{medicineInfo.productName}</Descriptions.Item>
                <Descriptions.Item label="通用名">{medicineInfo.commonName}</Descriptions.Item>
                <Descriptions.Item label="类型">{medicineInfo.drugType}</Descriptions.Item>
                <Descriptions.Item label="剂型">{medicineInfo.dosage}</Descriptions.Item>
                <Descriptions.Item label="处方药">{medicineInfo.otcRx}</Descriptions.Item>
                <Descriptions.Item label="产品规格">
                  {medicineInfo.productStandard}
                </Descriptions.Item>
                <Descriptions.Item label="包装单位">{medicineInfo.packageUnit}</Descriptions.Item>
                <Descriptions.Item label="企业名称">{medicineInfo.companyName}</Descriptions.Item>
                <Descriptions.Item label="批准文号">
                  {medicineInfo.approvalNumber}
                </Descriptions.Item>
                <Descriptions.Item> </Descriptions.Item>
                <Descriptions.Item label="注册证号">
                  {medicineInfo.registrationNumber}
                </Descriptions.Item>
              </Descriptions>
              <Divider />
              <Row gutter={16} style={{ display: 'none' }}>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="drugName" label="药品名称" rules={[{ required: true }]}>
                    <Input disabled placeholder="药品名称" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="productName" label="商品名">
                    <Input disabled placeholder="商品名" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16} style={{ display: 'none' }}>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="commonName" label="通用名">
                    <Input disabled placeholder="通用名" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="drugType" label="类型" rules={[{ required: true }]}>
                    <Input disabled placeholder="类型" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16} style={{ display: 'none' }}>
                <Col className="gutter-row" span={6}>
                  <Form.Item name="dosage" label="剂型" rules={[{ required: true }]}>
                    <Input disabled placeholder="剂型" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={6}>
                  <Form.Item name="otcRx" label="是否处方药">
                    <Input disabled placeholder="是否处方药" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="productStandard" label="产品规格" rules={[{ required: true }]}>
                    <Input disabled placeholder="产品规格" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16} style={{ display: 'none' }}>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="packageUnit" label="包装单位" rules={[{ required: true }]}>
                    <Input disabled placeholder="包装单位" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="companyName" label="企业名称" rules={[{ required: true }]}>
                    <Input disabled placeholder="企业名称" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16} style={{ display: 'none' }}>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="approvalNumber" label="批准文号">
                    <Input disabled placeholder="批准文号" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="registrationNumber" label="注册证号">
                    <Input disabled placeholder="注册证号" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="uniqueId" label="唯一ID">
                    <Input disabled placeholder="唯一ID" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="packageSpecification" label="规格">
                    <Input disabled placeholder="规格" />
                  </Form.Item>
                </Col>
              </Row>
            </div>
            <div className={styles.infoBox}>
              <div className={styles.title}>设置价格/库存</div>
              <Row>
                <Col className="gutter-row" span={12}>
                  <Form.Item
                    name="salePrice"
                    label="零售价"
                    rules={[
                      {
                        required: true,
                        message: '请输入零售价',
                      },
                      {
                        pattern: /^[^ ]+$/g,
                        message: '前后不能输入空格',
                      },
                      {
                        pattern: /(^(\d|[1-9]\d{1,5})(\.\d{1,2})?$)|(^999999$)/,
                        message: '请输入有效的零售价',
                      },
                    ]}
                  >
                    <Input
                      disabled={medicineInfo.state === '1'}
                      suffix="元"
                      placeholder="请输入零售价"
                    />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item
                    name="stockQuantity"
                    label="库存"
                    rules={[
                      {
                        required: true,
                        message: '库存不能为空!',
                      },
                      {
                        pattern: /^\+?[0-9]\d{0,5}$/,
                        message: '请输入有效的库存',
                      },
                    ]}
                  >
                    <Input disabled={medicineInfo.state === '1'} placeholder="库存" />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item name="stockState" valuePropName="checked" label="库存状态">
                    <Checkbox disabled={medicineInfo.state === '1'}>不限库存</Checkbox>
                  </Form.Item>
                </Col>
              </Row>
            </div>
          </>
        ) : (
          <NotFoundContent
            imageStyle={{ height: 300 }}
            style={{ padding: '80px 0' }}
            image={SearchIcon}
            description=""
          />
        )}
      </Form>
    </Modal>
  );
};

export default AddMedicine;
