import React, { forwardRef, useImperativeHandle } from 'react';
import { Col, Row, Select, Spin, Tooltip } from '@sinohealth/butterfly-ui-antd/lib';
import { SelectProps } from '@sinohealth/butterfly-ui-components';
// import debounce from 'lodash/debounce';
import { SearchOutlined } from '@ant-design/icons';
import NotFoundContent from '../NotFoundContent';
import { deBounce } from '@/utils';

export interface DebounceSelectProps<ValueType = any>
  extends Omit<SelectProps<ValueType>, 'options' | 'children'> {
  fetchOptions: (search: string) => Promise<ValueType[]>;
  debounceTimeout?: number;
}

const { Option } = Select;

function MedicineSearch<
  ValueType extends { key?: string; label: React.ReactNode; value: string | number } = any,
>({ fetchOptions, debounceTimeout = 800, ...props }: DebounceSelectProps, ref: any) {
  const [fetching, setFetching] = React.useState(false);
  const [options, setOptions] = React.useState<ValueType[]>([]);
  const [isShowNotFound, setIsShowNotFound] = React.useState(false);
  const fetchRef = React.useRef(0);

  useImperativeHandle(ref, () => ({ clearOptions }));

  const clearOptions = () => {
    setOptions([]);
  };

  const debounceFetcher = React.useMemo(() => {
    const loadOptions = (value: string) => {
      fetchRef.current += 1;
      const fetchId = fetchRef.current;
      setOptions([]);
      setFetching(true);
      fetchOptions(value).then((newOptions) => {
        if (fetchId !== fetchRef.current) {
          // for fetch callback order
          return;
        }

        setOptions(newOptions);
        if (newOptions.length <= 0) {
          setIsShowNotFound(true);
        } else {
          setIsShowNotFound(false);
        }
        setFetching(false);
      });
    };

    return deBounce(loadOptions, debounceTimeout);
  }, [fetchOptions, debounceTimeout]);

  return (
    <Select<ValueType>
      labelInValue
      filterOption={false}
      onSearch={debounceFetcher}
      showSearch
      onFocus={() => {
        setIsShowNotFound(false);
      }}
      onBlur={() => {
        setIsShowNotFound(false);
      }}
      // eslint-disable-next-line no-nested-ternary
      notFoundContent={fetching ? <Spin /> : isShowNotFound ? <NotFoundContent /> : null}
      {...props}
      // options={options}
      optionLabelProp="label"
      suffixIcon={<SearchOutlined />}
    >
      {options.map((item: any) => (
        <Option key={item.value} value={item.value} label={item.label}>
          <Row wrap={false} style={{ height: 20 }}>
            <Col
              flex="auto"
              style={{
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                lineHeight: '20px',
              }}
            >
              <Tooltip placement="topLeft" title={item.label}>
                {item.label}
              </Tooltip>
            </Col>
            <Col flex="none">
              <div style={{ padding: '0 16px', lineHeight: '20px' }}>
                {item.state === '0' ? (
                  <span style={{ color: '#ec7785' }}>未入库</span>
                ) : (
                  <span style={{ color: '#52c41a' }}>已入库</span>
                )}
              </div>
            </Col>
          </Row>
        </Option>
      ))}
    </Select>
  );
}

