import request from '@/core/request';

/**
 *
 * @param url
 * @param params
 * @returns
 */
const postReturnAllData = (url: string, params: any) =>
  // @ts-ignore
  request.post(url, params, { isReturnAllData: true });
/**
 *
 * @param params
 * @returns
 */
export const queryByCommonNameOrApprovalNumber = (params: { searchText: string; type?: string }) =>
  request.post('/api/pharmacy/drugStock/queryByCommonNameOrApprovalNumber', params);

/**
 * 分页查询药品库
 * @param params
 * @returns
 */
export const pagePharmacyDrugStock = (params: GOOD.pagePharmacyDrugStockReq) =>
  request.post('/api/pharmacy/drugStock/queryList', params);

/**
 * 更改对应药品的状态 上下架、删除等
 * @param params
 * @returns
 */
export const pharmacyDrugStockChangeState = (params: {
  drugId: number | string;
  state: 'on_sale' | 'off_sale' | 'deleted';
}) => postReturnAllData('/api/pharmacy/drugStock/changeState', params);

export const pharmacyStockStateCount = (params: GOOD.pagePharmacyDrugStockReqEntity) =>
  request.post('/api/pharmacy/drugStock/stockStateCount', params);

/**
 * 修改库存
 * @param params
 * @returns
 */
export const pharmacyChangeStock = (params: {
  drugId: string | number;
  stock: number;
  state?: string;
}) => postReturnAllData('/api/pharmacy/drugStock/changeStock', params);

/**
 * 修改零售价
 * @param params
 * @returns
 */
export const pharmacyChangeSalePrice = (params: { drugId: string | number; salePrice: string }) =>
  request.post('/api/pharmacy/drugStock/changeSalePrice', params);

/**
 * 保存/新增药品
 * @param params
 * @returns
 */
export const pharmacyDrugStockSave = (params: GOOD.pharmacyDrugStockSaveReq) =>
  postReturnAllData('/api/pharmacy/drugStock/save', params);

export const pharmacyConfigDetail = () => request.get('/api/pharmacy/config/detail', {});

/**
 *
 * @param stockWarnNum
 * @param state
 * @returns
 */
export const pharmacyConfigSave = (stockWarnNum: number, state: string) =>
  request.post('/api/pharmacy/config/save', { stockWarnNum, state });
