import { useEffect, useState } from 'react';
import { Checkbox, Form, Input, message, Modal } from '@sinohealth/butterfly-ui-components/lib';
import { pharmacyChangeStock } from '@/services/goods';
// import { ModalForm, ProFormCheckbox, ProFormText } from '@ant-design/pro-form';
import styles from './index.less';

const waitTime = (time: number = 100) =>
  new Promise((resolve) => {
    setTimeout(() => {
      resolve(true);
    }, time);
  });
export type StockQuantityModelDataType = {
  id: string | number;
  stockQuantity?: string | number;
  stockState?: string;
  globalStockState?: string;
};
export type StockQuantityModelType = {
  onCancel: (success?: boolean) => void;
  visible: boolean;
  data: StockQuantityModelDataType;
};
export default (props: StockQuantityModelType) => {
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (props.data) {
      form.setFieldsValue({
        // stock: props.data.stockQuantity,
        stock: '',
        show: props.data.stockQuantity,
        state: props.data.stockState === 'unlimited',
      });
    }
  }, [props.data]);
  return (
    <Modal
      visible={props.visible}
      onOk={() => {
        form.submit();
      }}
      width={450}
      closable={false}
      cancelButtonProps={{ type: 'info' }}
      onCancel={() => {
        props.onCancel();
      }}
      confirmLoading={loading}
      className={styles['stock-quantity-model']}
    >
      <h2 className={styles.title}>修改库存</h2>
      <Form
        form={form}
        layout="horizontal"
        labelCol={{ span: 6 }}
        onFinish={async (values: any) => {
          setLoading(true);
          await waitTime(2000);
          console.log(values);
          const params = {
            drugId: props.data.id,
            stock: values.stock || props.data.stockQuantity,
            state: values.state ? 'unlimited' : 'limited',
          };

          pharmacyChangeStock(params)
            .then((res: any) => {
              if (res.code === 200) {
                message.success('提交成功');
                props.onCancel(true);
              }
            })
            .finally(() => {
              setLoading(false);
            });
          return true;
        }}
      >
        <Form.Item label="当前库存量" name="show" style={{ marginBottom: 10 }}>
          <Input bordered={false} placeholder="请输入" />
        </Form.Item>
        <Form.Item
          name="stock"
          label="修改库存量"
          rules={[
            {
              pattern: /^\+?[1-9]\d{0,5}$/,
              message: '请输入有效的库存',
            },
          ]}
        >
          <Input
            disabled={props.data?.globalStockState === '0'}
            autoComplete="off"
            placeholder="请输入"
          />
        </Form.Item>

        <Form.Item name="state" valuePropName="checked" label="" className={styles.ext}>
          <Checkbox disabled={props.data?.globalStockState === '0'}>不限库存</Checkbox>
        </Form.Item>
      </Form>
    </Modal>
  );
};
