import React, { ReactNode, useEffect, useState } from 'react';
import {
  Button,
  Col,
  Form,
  Input,
  Row,
  DatePicker,
  Cascader,
  Select,
  InputNumber,
} from '@sinohealth/butterfly-ui-antd/lib';
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import styles from './index.less';
import { useDict } from '@/hooks/useDict';

const { RangePicker } = DatePicker;
const { Option } = Select;

type SearchFormProps = {
  onFinish: (values: any) => void;
};

/**
 * 订单查询-查询表单
 * @returns
 */
const SearchForm: React.FC<SearchFormProps> = (props) => {
  const { onFinish } = props;
  const [form] = Form.useForm();
  const [expand, setExpand] = useState(false);
  const [maxHeight, setMaxHeight] = useState<string>('78px');
  const { dictObj } = useDict();

  const displayRender: any = (labels: any[], selectedOptions: { [x: string]: any }) =>
    labels.map((label, i) => {
      const option = selectedOptions[i];
      if (i === labels.length - 1) {
        return (
          <span key={option.value}>
            {typeof label === 'string' ? `${label}/` : `${label.props.text}`}
          </span>
        );
      }
      return (
        <span key={option.value}>
          {console.log(typeof label === 'string' ? `${label}` : `${label.props.text}`)}
          {typeof label === 'string' ? `${label}/` : `${label.props.text}`}
        </span>
      );
    });

  return (
    <div className={styles['order-table-container']} style={{ height: maxHeight }}>
      <Form
        form={form}
        name="advanced_search"
        labelCol={{ span: 6 }}
        className="ant-advanced-search-form"
        onFinish={(values: any) => {
          const copy = { ...values };
          console.log(copy);
          onFinish(copy);
        }}
      >
        <Row>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item name="commonName" label="药品通用名">
              <Input allowClear placeholder="请输入药品通用名" autoComplete="off" />
            </Form.Item>
          </Col>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item name="drugType" label="药品类别">
              <Select style={{ width: '100%' }} placeholder="请选择" allowClear>
                {dictObj &&
                  Object.keys(dictObj.drugCategory).map((key: any) => (
                    <Option value={key} key={key}>
                      {dictObj.drugCategory[key]}
                    </Option>
                  ))}
              </Select>
            </Form.Item>
          </Col>
          <Col span={8} style={{ textAlign: 'right' }}>
            <Button
              onClick={() => {
                form.resetFields();
                // onFinish({});
              }}
            >
              重置
            </Button>
            <Button style={{ margin: '0 16px' }} type="primary" htmlType="submit">
              查询
            </Button>
            <span
              style={{ cursor: 'pointer' }}
              onClick={() => {
                setExpand(!expand);
                setMaxHeight(expand ? '78px' : 'auto');
              }}
            >
              {expand ? <UpOutlined /> : <DownOutlined />}
            </span>
          </Col>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item label="零售价" style={{ marginBottom: 0 }}>
              <Form.Item
                name="salePriceStart"
                style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}
              >
                <Input allowClear />
              </Form.Item>
              <span
                style={{
                  display: 'inline-block',
                  width: '24px',
                  lineHeight: '32px',
                  textAlign: 'center',
                }}
              >
                -
              </span>
              <Form.Item
                name="salePriceEnd"
                label=""
                style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}
              >
                <Input allowClear />
              </Form.Item>
            </Form.Item>
          </Col>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item label="库存" style={{ marginBottom: 0 }}>
              <Form.Item
                name="stockStart"
                style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}
              >
                <Input allowClear />
              </Form.Item>
              <span
                style={{
                  display: 'inline-block',
                  width: '24px',
                  lineHeight: '32px',
                  textAlign: 'center',
                }}
              >
                -
              </span>
              <Form.Item
                name="stockEnd"
                label=""
                style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}
              >
                <Input allowClear />
              </Form.Item>
            </Form.Item>
          </Col>
          <Col span={8} style={{ paddingRight: 60 }}>
            <Form.Item label="创建日期" name="creationDate">
              <RangePicker placeholder={['起始日期', '结束日期']} />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </div>
  );
};

export default SearchForm;
