import React from 'react';
import { Empty } from '@sinohealth/butterfly-ui-antd/lib';
import notImage from '@/assets/images/common/not.png';

type NotFoundContentProps = {
  style?: React.CSSProperties;
  image?: any;
  description?: string;
  imageStyle?: React.CSSProperties;
};
/**
 * 空状态
 * @param props
 * @returns
 */
const NotFoundContent: React.FC<NotFoundContentProps> = (props) => {
  return (
    <div style={props.style}>
      <Empty
        image={props.image}
        imageStyle={props.imageStyle}
        style={{ color: 'rgba(150, 151, 153, 1)' }}
        description={<span>{props.description}</span>}
      />
    </div>
  );
};
NotFoundContent.defaultProps = {
  style: { padding: '50px 0' },
  image: notImage,
  description: '暂无信息',
  imageStyle: {
    height: 60,
  },
};
export default NotFoundContent;
