import React, { useRef, useEffect, useState } from 'react';
import { Modal, Table } from '@sinohealth/butterfly-ui-antd/lib';
import { FormOutlined, QuestionCircleTwoTone } from '@ant-design/icons';
import moment from 'moment';
import { ColumnsType, ColumnType } from '@sinohealth/butterfly-ui-antd/lib/table';
import styles from './index.less';
import { pagePharmacyDrugStock, pharmacyDrugStockChangeState } from '@/services/goods';
import StockQuantityModel from '../StockQuantityModel';
import type { StockQuantityModelDataType } from '../StockQuantityModel';
import SalePriceModel from '../SalePriceModel';
import type { SalePriceModelDataType } from '../SalePriceModel';
import ConfirmModel from '@/components/Confirm';

const { confirm } = Modal;

interface IProps {
  type: string;
  headSearch?: any;
  currType: string;
  onFetchTabStockStateCount: any;
  time?: any;
}

const TableData: React.FC<IProps> = (props) => {
  const actionRef = useRef<any>();
  const [stockQuantityModalVisit, setStockQuantityModalVisit] = useState(false); // 修改库存弹窗
  const [salePriceModelVisit, setSalePriceModelVisit] = useState(false); // 修改零售价弹窗
  const [stockQuantityModalData, setStockQuantityModalData] = useState<StockQuantityModelDataType>({
    id: '',
  }); // 修改库存弹窗

  const [salePriceModelData, setSalePriceModelData] = useState<SalePriceModelDataType>({
    id: '',
    salePrice: '',
  });

  const [firtsReq, setFirtsReq] = useState<boolean>(true);
  // 表格数据
  const [tableData, setTabledata] = useState<any[]>([]);
  const [total, setTotal] = useState<any>();

  const [entity, setEntity] = useState<any>({});
  const [tableLoading, setTableLoading] = useState<boolean>(false);
  const [pageData, setPageData] = useState<{ pageNo: number; pageSize: number }>({
    pageNo: 1,
    pageSize: 10,
  });

  const getTableData = async (paramEntity: any = {}, paramPage: any = {}) => {
    if (tableLoading) {
      return;
    }
    const options = {
      ...pageData,
      ...paramPage,
      entity: {
        type: props.type,
        ...entity,
        ...paramEntity,
      },
    };
    setTableLoading(true);
    pagePharmacyDrugStock(options)
      .then((res: any) => {
        setTabledata(res.records);
        setTotal(res.total);
        setTableLoading(false);
        if (res.pageNo !== pageData.pageNo || res.pageSize !== pageData.pageSize) {
          setPageData({ pageNo: res.pageNo, pageSize: res.pageSize });
        }
      })
      .catch(() => {
        setTableLoading(false);
      });
  };

  const columns: ColumnsType<any> = [
    {
      title: '序号',
      // valueType: 'index',
      width: 70,
      dataIndex: 'id',
      ellipsis: true,
      render: (_, record, index) => {
        return <div>{(pageData.pageNo - 1) * pageData.pageSize + (index + 1)}</div>;
      },
    },
    {
      title: '药品名称',
      dataIndex: 'drugName',
      ellipsis: true,
      width: 150,
    },
    {
      title: '商品名',
      dataIndex: 'productName',
      width: 150,
      ellipsis: true,
    },
    {
      title: '通用名',
      width: 200,
      dataIndex: 'commonName',
      ellipsis: true,
    },
    {
      title: '药品类型',
      dataIndex: 'drugType',
      width: 90,
    },
    {
      title: '剂型',
      dataIndex: 'dosage',
      width: 120,
    },
    {
      title: '产品规格',
      dataIndex: 'productStandard',
      ellipsis: true,
    },
    {
      title: '单位',
      dataIndex: 'packageUnit',
      width: 60,
    },
    {
      title: '企业名称',
      dataIndex: 'companyName',
      ellipsis: true,
      width: 200,
    },
    {
      title: '批准文号',
      dataIndex: 'approvalNumber',
      ellipsis: true,
      width: 160,
    },
    {
      title: '冻结库存',
      dataIndex: 'freezeQuantity',
      width: 100,
    },
    {
      title: '库存',
      dataIndex: 'stockQuantity',
      render: (_: any, record: any) => (
        <>
          <span className={styles.iconText}>
            {record.stockState === 'unlimited' ? '不限' : record.stockQuantity}
          </span>
          <FormOutlined
            style={{ color: '#1791FF', cursor: 'pointer' }}
            onClick={() => {
              if (record.globalStockState === '0') {
                confirm({
                  cancelText: '关闭',
                  title: '提示',
                  content:
                    '全局设置-不限库存已开启，如想修改该药品库存，请先到全局设置，关闭该开关。',
                });
              } else {
                setStockQuantityModalVisit(true);
                setStockQuantityModalData({
                  id: record.id,
                  stockQuantity: record.stockQuantity || '',
                  stockState: record.stockState,
                  globalStockState: record.globalStockState,
                });
              }
            }}
          />
        </>
      ),
    },
    {
      title: '零售价',
      dataIndex: 'salePrice',
      width: 100,
      render: (_: any, record: any) => (
        <>
          <span className={styles.iconText}>{record.salePrice}</span>
          <FormOutlined
            style={{ color: '#1791FF', cursor: 'pointer' }}
            onClick={() => {
              setSalePriceModelVisit(true);
              setSalePriceModelData({
                id: record.id,
                salePrice: record.salePrice || '',
              });
            }}
          />
        </>
      ),
    },
    {
      title: '创建时间',
      width: 140,
      key: 'since',
      dataIndex: 'createTime',
      ellipsis: true,
      // sorter: true,
      render: (text: any, record: any) => moment(text).format('YYYY-MM-DD HH:mm'),
    },
    {
      title: '操作',
      fixed: 'right',
      width: 160,
      render: (text: any, record: any) => (
        <div>
          <a
            key="offTheShelf"
            style={{ color: '#FFC100', marginLeft: 20 }}
            onClick={() => {
              console.log(text, record);
              ConfirmModel({
                fun: 'warning',
                title: record.state === 'off_sale' ? '上架药品' : '下架药品',
                subtitle:
                  record.state === 'off_sale' ? '是否确定上架该药品：' : '是否确定下架该药品：',
                node: (
                  <div>
                    {`${record.approvalNumber} | ${record.productName} / ${record.drugType}${
                      record.dosage
                    } ${record.spec || record.productStandard}`}
                    {record.state === 'off_sale' || (
                      <div className={styles.ext}>*可在待上架栏找回药品并重新上架</div>
                    )}
                  </div>
                ),
                centered: true,
                // icon: <QuestionCircleTwoTone twoToneColor="#FFBF00" />,
                onOk: async () => {
                  // 修改状态的类型
                  const res: any = await pharmacyDrugStockChangeState({
                    drugId: record.id,
                    state: record.state === 'off_sale' ? 'on_sale' : 'off_sale',
                  });
                  if (res?.code === 200) {
                    getTableData();
                    props.onFetchTabStockStateCount();
                  }
                },
              });
            }}
          >
            {record.state === 'off_sale' ? '上架' : '下架'}
          </a>
          {record.state !== 'deleted' && (
            <a
              key="delete"
              style={{ color: '#F53F3F', marginLeft: 20 }}
              onClick={() => {
                console.log(text, record);
                ConfirmModel({
                  fun: 'error',
                  title: '删除药品',
                  subtitle: '是否确定删除该药品：',
                  node: `${record.approvalNumber} | ${record.productName} / ${record.drugType}${
                    record.dosage
                  } ${record.spec || record.productStandard}`,
                  onOk: async () => {
                    const res: any = await pharmacyDrugStockChangeState({
                      drugId: record.id,
                      state: 'deleted',
                    });
                    if (res?.code === 200) {
                      getTableData();
                      props.onFetchTabStockStateCount();
                    }
                  },
                });
              }}
            >
              删除
            </a>
          )}
        </div>
      ),
    },
  ];

  useEffect(() => {
    setEntity({ ...props.headSearch });
    if (props.currType === props.type && !firtsReq) {
      getTableData(props.headSearch, { pageNo: 1, pageSize: pageData.pageSize });
    }
  }, [props.headSearch]);

  useEffect(() => {
    if (firtsReq) {
      setFirtsReq(false);
    }
    if (props.currType === props.type) {
      getTableData();
    }
  }, [props.currType, props.time]);

  return (
    <div className={styles.table_contaoner}>
      <Table
        loading={tableLoading}
        dataSource={tableData}
        columns={columns}
        scroll={{ x: 2000 }}
        rowKey="id"
        className={styles.table_contaoner}
        pagination={{
          position: ['bottomRight'],
          showSizeChanger: true,
          total,
          onChange: (page, pageSize) => {
            setPageData({ pageNo: page, pageSize });
            getTableData({}, { pageNo: page, pageSize });
          },
        }}
      />
      <StockQuantityModel
        visible={stockQuantityModalVisit}
        data={stockQuantityModalData}
        onCancel={(success?: boolean) => {
          if (success) {
            props.onFetchTabStockStateCount(entity);
            getTableData();
          }
          setStockQuantityModalVisit(false);
        }}
      />
      <SalePriceModel
        visible={salePriceModelVisit}
        data={salePriceModelData}
        onCancel={(success?: boolean) => {
          if (success) {
            props.onFetchTabStockStateCount(entity);
            getTableData();
          }
          setSalePriceModelVisit(false);
        }}
      />
    </div>
  );
};

export default TableData;
